import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable()
export class LoginService {

 private _jsonURL = 'assets/data/alunos.json';

 constructor(private http: HttpClient) {

 }


 public getAlunos(): Promise<any> {
   return new Promise(
     (resolve, reject) => {
        this.http.get(this._jsonURL).subscribe(
          (data) => {
            return resolve(data['alunos'])
          },
          (error) => {
            return reject(error)
          }
        )
     }
   )
 }

 public autenticar(ano, passwd)
 {
   if(ano != 0 && ano != 9 && ano != '0' && ano != '9')
   {
    if(passwd == 'institutoserrano'+ano+'ano')
    {
      return true
    }
    else
    {
      return false
    }
   }
   else
   {
     if(passwd == 'institutoserrano@2020')
     {
       return true
     }
     else
     {
       return false
     }
   }

 }

 public getAllClasses(matr): Promise<any[]>
 {
   return new Promise(
      (resolve, reject) => {
        if(parseInt(matr+'') < 10)
        {
          this.http.get(this._jsonURL).subscribe(
            (data) => {
              return resolve(data['all_access'])
            },
            (error) => {
              return reject(error)
            }
          )
        }
        else
        {
          return reject("permission denied")
        }
      }
    )
 }

}
