import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { LoginService } from '../login-page/login.service';
declare var $: any;


@Component({
  selector: 'app-inicio-page',
  templateUrl: './inicio-page.component.html',
  styleUrls: ['./inicio-page.component.scss'],
  providers: [LoginService]
})
export class InicioPageComponent implements OnInit {

  time_msg = ""
  time_to_show = 0
  minutes_to_show = 0
  aluno = {
    "matricula": "",
    "nome": "",
    "ano": "",
    "professora": "",
    "inicio": "",
    "fim": "",
    "url": ""
  }

  dia_semana = 0;
  intMatr = 0

  show_help = true

  classes = []


  constructor(private route: Router, private service: LoginService) { }

  ngOnInit(): void {
    let times_msgs = [
      "dias", "horas", "minutos", "segundos", "online"
    ]
    this.dia_semana = new Date().getDay();

   let aluno = JSON.parse(localStorage.getItem("instserraluno"))

   if(aluno)
   {
     console.log(aluno)
     this.aluno = aluno['aluno']
     let last_login = new Date(aluno["time"]).getTime()
     let now = new Date().getTime()

     if(((now - last_login) / (1000 * 60 )) > 50)
     {
        this.route.navigate(['/login'])
     }

     this.intMatr = parseInt(aluno['aluno']['matricula']+'')
     if(this.intMatr < 10)
     {
        this.service.getAllClasses(this.aluno['matricula']).then( (classes) => {this.classes = classes}, (error) => {alert("erro ao obter os acessos")} )
     }
   }
   else
   {
     this.route.navigate(['/login'])
   }

    this.getTime()
  }

  getTime()
  {

    let inicio = parseInt(this.aluno.inicio)
    let fim = parseInt(this.aluno.fim)

    setInterval( () => {
      let now = new Date()

      let tomorrow  = new Date(now)
      tomorrow.setDate(tomorrow.getDate() + 1)
      tomorrow.setHours(inicio)
      tomorrow.setMinutes(0)
      tomorrow.setSeconds(0)

      let today_after = new Date(now)
      today_after.setHours(inicio)
      today_after.setMinutes(0)
      today_after.setSeconds(0)


      if(now.getDay() == 6 || now.getDay() == 0)
      {
        this.time_to_show = (8 - now.getDay()) % 7;
        this.time_msg = "dias"
      }
      else if(now.getHours() == inicio)
      {
        this.minutes_to_show = 0
        this.time_to_show = 0
        this.time_msg = 'online'
        if(now.getHours() >= fim)
        {
          this.time_msg = 'acabou'
          this.time_to_show = 0
          this.minutes_to_show = 0
        }
      }
      else if((now.getHours() > (inicio + 4)) && (((tomorrow.getTime() - now.getTime()) / (1000 * 60 * 60)) > 0))
      {
        this.time_msg = "horas"
        this.time_to_show = parseInt(((tomorrow.getTime() - now.getTime()) / (1000 * 60 * 60)).toString().split('.')[0])
        this.minutes_to_show = parseInt(((tomorrow.getTime() - now.getTime()) / (1000 * 60 * 60)).toFixed(2).toString().split('.')[1])
        this.minutes_to_show = this.minutes_to_show - (this.minutes_to_show % 60)  - (this.minutes_to_show % 100)
      }
      else if((now.getHours() < (inicio + 4)) && (((today_after.getTime() - now.getTime()) / (1000 * 60 * 60)) >= 1))
      {
        this.time_msg = "horas"
        this.time_to_show = parseInt(((today_after.getTime() - now.getTime()) / (1000 * 60 * 60)).toString().split('.')[0])

        this.minutes_to_show = parseInt(((today_after.getTime() - now.getTime()) / (1000 * 60 * 60)).toFixed(2).toString().split('.')[1])

        this.minutes_to_show = parseInt((60 * parseFloat("0."+ this.minutes_to_show)).toString().split('.')[0])

      }
      else if(((today_after.getTime() - now.getTime()) / (1000 * 60 )) > 0)
      {
        this.time_msg = "minutos"
        this.time_to_show = parseInt(((today_after.getTime() - now.getTime()) / (1000 * 60)).toString().split('.')[0])
      }
      else if(((today_after.getTime() - now.getTime()) / (1000 )) > 0)
      {
        this.time_msg = "segundos"
        this.time_to_show = parseInt(((today_after.getTime() - now.getTime()) / (1000)).toString().split('.')[0])
      }

    },1000)



  }

}
