import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl } from '@angular/forms';
import { LoginService } from './login.service';
import { Router } from '@angular/router';

declare var $: any;


@Component({
  selector: 'app-login-page',
  templateUrl: './login-page.component.html',
  styleUrls: ['./login-page.component.scss'],
  providers: [LoginService]
})
export class LoginPageComponent implements OnInit {

  formLogin = new FormGroup({'matricula': new FormControl(''), 'senha': new FormControl('')})


  constructor(private service: LoginService, public router: Router) { }

  ngOnInit(): void {

    /*==================================================================
    [ Focus Contact2 ]*/
    $('.input100').each(function(){
      $(this).on('blur', function(){
          if($(this).val().trim() != "") {
              $(this).addClass('has-val');
          }
          else {
              $(this).removeClass('has-val');
          }
      })
  })


  /*==================================================================
  [ Validate ]*/
  var input = $('.validate-input .input100');

  $('.validate-form').on('submit',function(){
      var check = true;

      for(var i=0; i<input.length; i++) {
          if(validate(input[i]) == false){
              showValidate(input[i]);
              check=false;
          }
      }

      return check;
  });


  $('.validate-form .input100').each(function(){
      $(this).focus(function(){
         hideValidate(this);
      });
  });

  function validate (input) {
      if($(input).attr('type') == 'email' || $(input).attr('name') == 'email') {
          if($(input).val().trim().match(/^([a-zA-Z0-9_\-\.]+)@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.)|(([a-zA-Z0-9\-]+\.)+))([a-zA-Z]{1,5}|[0-9]{1,3})(\]?)$/) == null) {
              return false;
          }
      }
      else {
          if($(input).val().trim() == ''){
              return false;
          }
      }
  }

  function showValidate(input) {
      var thisAlert = $(input).parent();

      $(thisAlert).addClass('alert-validate');
  }

  function hideValidate(input) {
      var thisAlert = $(input).parent();

      $(thisAlert).removeClass('alert-validate');
  }
  }

  keyDownFunction(event) {
    if(event.keyCode == 13) {
     this.login()
    }
  }


  login()
  {
    let credentials = this.formLogin.value;
    if(credentials['matricula'])
    {
      this.searchForStudent(credentials['matricula'].toString()).then( (aluno) => {
        if(this.service.autenticar(aluno['ano'], credentials['senha']))
        {
          localStorage.setItem("instserraluno", JSON.stringify({'aluno': aluno, 'time': new Date()}))

          this.router.navigate(['/inicio']);
        }
        else
        {
          alert('senha incorreta')
        }
      })
    }


  }

  searchForStudent(matr): Promise<any>
  {
    return new Promise((resolve, reject) => {
      this.service.getAlunos().then( (alunos) => {
        let aluno = alunos.find(al => al.matricula === matr)
        return resolve(aluno)
      },
      (error) => {
        alert("erro ao fazer o login")
      })
    })

  }

}

